import { Pipe, PipeTransform } from '@angular/core';
import { NotificationItem } from '../models/notification-item';

const linkRegexp = /{{(.*)}}/;

@Pipe({
  name: 'interpolateLink',
})
export class InterpolateLinkPipe implements PipeTransform {
  transform(
    template: string,
    value: NotificationItem,
    focusableLink: boolean = true,
    classLink = '',
  ): string | undefined {
    return template.replace(
      linkRegexp,
      (_: string, linkText: string) =>
        `<a${classLink && ' class="' + classLink + '"'} href="${
          value.link
        }" target="_blank" rel="noopener noreferrer" tabindex="${focusableLink ? '0' : '-1'}">${
          linkText || value.link
        }</a>`,
    );
  }
}
